.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --main-font: "Montserrat", sans-serif;
  --text-color: #fff;
  --accent-colour: #1e1405;
  --background-dark: #1e1405;
  --background-light: #fff;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

